.column-steps_count,
.column-is_active {
	text-align: left;
	width: 60px !important;
	overflow: hidden
}

.dpit-tour-active-state {
	&--yes {
		color: rgb(var(--dpit-uac-prim-bg));
	}

	&--no {
		color: rgba($text-color-dark, 0.95);
	}
}

.post-type-dp_intro_tours #wp-admin-bar-archive {
	display: none;
}

h1.wp-heading-inline {
	text-transform: uppercase;
	font-weight: 500;
}