.dpit-notice {

	//max-width: 750px;


	&--pro-promo {
		//border-left-color: rgb(var(--dpit-uac-prim-bg));
	}

	&#dpit-notice-create-first-tour,
	&#dp_intro_tours-a4r-notice {
		//border-left-color: rgb(var(--dpit-uac-prim-bg));
	}


	&__link {
		//display: block;
		margin-right: 15px;

		&:last-of-type {
			margin-bottom: 17px;
			display: inline-block;
		}

		&#a4r-link-OK {
			margin-bottom: 8px;
			padding-right: 50px;
			padding-left: 50px;
			font-weight: 600;
		}
	}

	&__text {
		padding-bottom: 10px !important;
	}

	&__button {
		margin-bottom: 17px !important;
		border: none !important;
		@include transitionFast(background-color);

		font-weight: 600;

		&__sub-text {
			margin-top: -8px;
			display: block;
			font-size: 10px;
			font-weight: 500;
			color: rgba(var(--dpit-uac-badge), 0.9);
			text-transform: uppercase;
			line-height: 2.5;
		}

		&--upper-case {
			text-transform: uppercase;
		}

		&--secondary {
			border: 1px solid rgba(var(--dpit-uac-sec-border), 0.9) !important;
			color: rgba(var(--dpit-uac-sec), 0.9) !important;

			.dpit-notice__button__sub-text {
				color: rgba(var(--dpit-uac-sec), 0.9);
			}
		}



		&.button-pro-promo {
			font-weight: 600;
			/*@include transitionFast(background-color);
			background: rgb(var(--dpit-uac-badge-bg)) !important;

			&:hover {
				background: rgba(var(--dpit-uac-badge-bg), 0.9) !important;
			}*/
		}
	}

	&__img {
		&.dpit-a4r-5star-img {
			max-width: 120px;
			margin: 7px 0 -7px;
		}
	}

	&--2-buttons {
		.dpit-notice__button:first-of-type {
			margin-right: 10px;
		}
	}
}


.dpit-notice-container {
	max-height: 200px;
	overflow-y: auto;
}