#menu-posts-dp_intro_tours {

	#adminmenu {
		.dpit-ab-li__icon {
			fill: rgb(var(--dpit-uac-main-menu));
		}
	}


	>.wp-submenu {
		li {
			>a::before {
				font-family: 'dashicons';
				padding-right: 0.3em;
				vertical-align: top;
			}

			&:nth-child(3) {
				>a::before {
					content: "\f132";
				}
			}

			&:nth-child(4) {
				>a::before {
					content: "\f111";
				}
			}

			&:nth-child(5) {
				>a::before {
					content: "\f468";
				}
			}

			&:nth-child(6) {
				>a::before {
					content: "\f155";
					color: rgb(var(--dpit-uac-badge-bg));
				}
			}
		}
	}
}