.dpit-admin-btn {

	position: relative;
	cursor: pointer;
	border-radius: 4px;
	max-width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: background-color $transition-time-fast ease, opacity $transition-time-slow ease;

	&--inactive {
		opacity: 0;
	}

	&__content {
		user-select: none;
		display: flex;
		justify-content: center;
		align-items: center;
		font-weight: 600;
		height: 100%;
		width: 100%;
		white-space: pre-wrap;
		@include transitionMultiFast(color);
	}



	&__anim {
		--bullet-anim-gap: 4px;
		--bullet-anim-size: 4px; // it is half size
		--bullet-anim-color: var(--dpit-uac-prim);
		--bullet-anim-off-alpha: 0.7;
		--bullet-anim-on-color: var(--dpit-uac-prim);

		.dpit-anim-rb__wrap {
			div {
				transition: transform 0.35s ease-out, background-color 0.220s ease-out !important;
			}
		}
	}

	&.button-primary {
		background-color: rgb(var(--dpit-uac-prim-bg));

		.dpit-admin-btn__content {
			color: rgba(var(--dpit-uac-prim), 1);
		}

		&:hover {
			background-color: rgb(var(--dpit-uac-prim-bg), 0.8) !important;
			border-color: rgb(var(--dpit-uac-prim-bg), 0.8) !important;

			.dpit-admin-btn__content {
				color: rgba(var(--dpit-uac-prim), 1);
			}
		}
	}

	&.button-secondary {
		.dpit-admin-btn__anim {
			--bullet-anim-off-alpha: 0.5;
			--bullet-anim-color: var(--dpit-uac-prim-bg);
			--bullet-anim-on-color: var(--dpit-uac-prim-bg);
		}
	}

	&--cta {
		display: inline-block;
		background-color: rgba(var(--dpit-uac-badge-bg), 0.85) !important;
		border-color: rgba(var(--dpit-uac-badge-bg), 0.85) !important;

		.dpit-admin-btn__content {
			color: rgba(var(--dpit-uac-badge), 1) !important;
		}

		&:hover {
			background-color: rgb(var(--dpit-uac-badge-bg)) !important;
			border-color: rgb(var(--dpit-uac-badge-bg)) !important;
		}
	}

	&--xl.dpit-admin-btn--cta,
	&--xl.button-primary,
	&--xl.button-secondary {
		padding: 11px 20px;

		.dpit-admin-btn__content {
			font-size: 1.1em;
			line-height: 1.6;
			font-weight: 600;
			letter-spacing: 0.3px;
			text-transform: uppercase;
		}
	}

	&--anim {
		pointer-events: none !important;

		.dpit-admin-btn__content {
			opacity: 0 !important;
		}
	}


	&:hover {
		background-color: rgb(var(--dpit-uac-prim-bg-hov));
	}


	&--error {
		pointer-events: none;

		&.button-primary {
			border-color: rgb(var(--dpu-color-r));
			background-color: rgb(var(--dpu-color-r));

			.dpit-admin-btn__content {
				color: rgb(var(--dpu-color-r-text));
			}
		}

		&.button-secondary {
			border-color: rgb(var(--dpu-color-r));

			.dpit-admin-btn__content {
				color: rgb(var(--dpu-color-r));
			}
		}
	}

	&--success {
		pointer-events: none;


		&.button-primary {
			border-color: rgb(var(--dpu-color-g));
			background-color: rgb(var(--dpu-color-g));

			.dpit-admin-btn__content {
				color: rgb(var(--dpu-color-g-text));
			}
		}

		&.button-secondary {
			border-color: rgb(var(--dpu-color-g));
			background-color: rgb(var(--dpu-color-g));

			.dpit-admin-btn__content {
				color: rgb(var(--dpu-color-g-text));
			}
		}
	}


	&--mobile-menu {

		box-shadow: 0 0 5px rgba(var(--dpu-color-4), .25);
		background-color: rgba(var(--dpu-color-4), 0.951);

		&:hover {
			background-color: rgba(var(--dpu-color-4), 0.75);
		}

		.dpit-admin-btn__text {
			color: rgba(var(--dpu-color-4-text), 1);
		}

		.dpit-admin-btn__bullets {
			ul {
				li {
					background-color: rgba(var(--dpu-color-4-text), 0.336);

					&.active {
						background-color: rgba(var(--dpu-color-4-text), 1) !important;
					}
				}
			}
		}
	}
}